* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Inter";
  color: black;
}

.header {
  width: 100%;
  line-height: 100px;
  background-color: orange;
  justify-content: start;
  align-items: center;
  padding-left: 50px;
  color: white;
  box-shadow: 5px 3px 15px rgba(0, 0, 0, 0.3);
}

.main_note {
  width: 500px;
  padding: 15px 10px 5px 10px;
  margin: 20px auto;
}

.my_notes h1 {
  color: #34495e;
}

.my_notes p {
  color: grey;
}

form {
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  position: relative;
}

input {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}

textarea {
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}

.Mui-Button-root {
  width: 30px;
  height: 50px;
  position: absolute;
  bottom: -28px;
  left: 400px;
  cursor: pointer;
  background-color: white !important;
  box-shadow: 5px 5px 15px -15px rgba(0, 0, 0, 0.3);
  border-radius: 50% !important;
}

.Mui-Button-root .plus_sign {
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}

.Mui-Button-root:hover {
  background: #f4b400 !important;
}

.Mui-Button-root:hover .plus_sign {
  color: white;
  background: transparent;
}

.note {
  background: white;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}

.note h1 {
  font-size: 1.1em;
  margin-bottom: 6px;
}

.note p {
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.note .btn {
  position: relative;
  float: right;
  margin-right: 10px;
  color: black;
  border: none;
  width: 35px;
  height: 35px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
}

.deleteIcon {
  width: 0.7em;
  height: 0.7em;
}

.note .btn:hover {
  background-color: orange;
}

.note .btn:hover .deleteIcon {
  color: white;
}

.note .save-btn {
  background-color: orange;
  color: white;
  border: none;
  margin-top: 5px;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
  transition: background-color 0.3s ease;
}

.note .save-btn:hover {
  background-color: orange;
}

.note-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px; /* Optional: Adjust the spacing as needed */
}

.color-picker {
  cursor: pointer;
}

.toggle-btn {
  padding: 5px;
  background: transparent;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.toggle-btn:hover {
  background-color: orange;
  color: white;
}